/*
    MDE default style
    Format support:less, regular css
    Author:chenxianming
*/
a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  font-style: normal;
}
b,
h1,
h2,
h3,
h4,
h5,
h6 {
  font: inherit;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
blockquote,
q {
  quotes: none;
}
blockquote:after,
blockquote:before,
q:after,
q:before {
  content: '';
  content: none;
}
* {
  margin: 0;
  padding: 0;
}
em,
i {
  font-style: italic;
}
html {
  font-family: "brelaregular";
}
.btn {
  cursor: pointer;
}
.btn:active {
  -webkit-transform: translate3d(0, 2px, 0);
          transform: translate3d(0, 2px, 0);
}
span {
  margin-top: -4px;
}
body,
html,
.App,
#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.logo,
.icon {
  -webkit-transition: all 0.45s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: all 0.45s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.Topbar {
  background: #3d3d3d;
  width: 100%;
  height: 60px;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  display: flex;
}
.logo {
  width: 120px;
  height: 60px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  display: flex;
}
.logo:hover {
  background: #515151;
}
.TopbarLeft {
  align-items: flex-start;
  justify-content: center;
  height: 100%;
  display: flex;
}
.MenuGroup {
  align-items: flex-start;
  justify-content: center;
  display: flex;
}
.MenuGroup:after {
  content: ' ';
  border: 0.5px solid #4b4b4b;
  height: 60px;
  margin: 0 5px;
}
.MenuGroup.unline:after {
  display: none;
}
.icon {
  width: 60px;
  height: 60px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #c6c6c6;
  display: flex;
}
.icon:hover,
.icon.active {
  background: #515151;
  color: #ffffff;
}
.Container {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  bottom: 0;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  background: #f5f5f5;
  display: flex;
}
.CodeEditor,
.CodePreview {
  width: 50%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.CodeEditor {
  background: #ffffff;
}
.CodePreview {
  background: #f5f5f5;
}
.editor {
  position: absolute;
  top: 10px;
  left: 60px;
  right: 10px;
  bottom: 10px;
  overflow: hidden;
  outline: 0;
  line-height: 24px;
}
.scroll {
  width: 100%;
  height: 100%;
}
.CodeLine {
  position: absolute;
  width: 50px;
  border-right: 1px solid #ebebeb;
  left: 0;
  top: 0;
  height: 100%;
  background: #f5f5f5;
  z-index: 99;
}
.CodeEditor:after {
  content: ' ';
  position: absolute;
  top: 0;
  width: calc(2% + 29px);
  background: #f7f7f7;
  border-right: 1px solid #ddd;
  left: 0;
  height: 100%;
  z-index: 0;
}
body .CodeMirror {
  z-index: 1;
}
.CodeMirror-gutter {
  z-index: 99;
}
.markdown-body {
  padding: 40px;
}
.CodeMirror {
  margin: 0 auto;
  line-height: 24px;
  font-size: 14px;
  position: relative;
}
.Drawer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  visibility: hidden;
}
.Drawer.active {
  visibility: visible;
}
.Drawer.active .DrawerMask {
  opacity: 0.6;
}
.Drawer.active .DrawerMenu {
  -webkit-transform: translate3d(0%, 0, 0);
          transform: translate3d(0%, 0, 0);
}
.DrawerMask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #3d3d3d;
  opacity: 0;
  cursor: pointer;
  -webkit-transition: all 0.45s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: all 0.45s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.DrawerMenu {
  width: 305px;
  height: 100%;
  background: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  -webkit-transition: all 0.45s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: all 0.45s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.DrawerNew {
  padding: 0 10px;
  height: 50px;
  width: 285px;
  align-items: center;
  justify-content: flex-start;
  margin-right: 10px;
  background: #d8d8d8;
  display: flex;
}
.DrawerNew span {
  font-size: 14px;
}
.NewDoc {
  width: 80px;
  height: 30px;
  align-items: center;
  justify-content: center;
  background: #3d3d3d;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  margin-right: 10px;
  color: #c6c6c6;
  display: flex;
}
.NewDoc i {
  margin-right: 5px;
}
.DocList:after {
  content: ' ';
  height: 10px;
  width: 100%;
  display: block;
}
.DocListItem {
  font-size: 14px;
  padding: 0 20px;
  line-height: 39px;
  height: 39px;
  border-bottom: 1px solid #c4c4c4;
  align-items: center;
  justify-content: space-between;
  display: flex;
  -webkit-transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
  cursor: default;
}
.DocListItem span {
  width: 190px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.DocListItem:hover,
.DocListItem.active {
  background: #f5f5f5;
}
.DocListItemActions {
  align-items: center;
  justify-content: space-between;
  display: flex;
}
.DocListItemAction {
  margin-left: 15px;
}
.BIconWrap {
  bottom: 20px;
  right: 20px;
  position: absolute;
  z-index: 1;
  height: 35px;
  display: flex;
}
.BIcon {
  width: 35px;
  height: 35px;
  background: #515151;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  color: #ffffff;
}
.Dialog {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.DialogMask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: ' ';
  background: #3d3d3d;
  opacity: 0.6;
  cursor: pointer;
}
.DialogContent {
  width: 500px;
  max-height: 500px;
  background: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -250px 0 0 -250px;
  border-radius: 5px;
  overflow: hidden;
  min-height: 150px;
}
.checkBtn {
  width: 100%;
  height: 45px;
  background: #eee;
}
.close {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  position: absolute;
}
.Dialog {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.Dialog.active {
  opacity: 1;
  visibility: visible;
}
.DialogTitle {
  display: block;
  text-align: center;
  margin-top: 30px;
  font-size: 24px;
  margin-bottom: 20px;
}
.DialogDescription {
  line-height: 24px;
  color: #3d3d3d;
  opacity: 0.7;
  padding: 0 20px;
  font-size: 14px;
}
.DialogActions {
  padding: 20px;
}
.checkBtn {
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ececec;
}
.checkBtn.sure {
  background: #3d3d3d;
  color: #ffffff;
}
.checkBtn {
  margin-bottom: 10px;
}
.checkBtn:last-child {
  margin-bottom: 0;
}
.InputWrap {
  border: 2px solid #d8d8d8;
  margin-bottom: 10px;
  padding: 10px;
}
.InputWrap.hor {
  display: inline-block;
  width: 200px;
}
.InputWrap.hor:nth-child(1) {
  margin-right: 10px;
}
.codeWrap {
  height: 200px;
  margin-bottom: 10px;
  border: 2px solid #d8d8d8;
  padding: 0;
}
.Input {
  margin: 0;
  height: 15px;
  line-height: 15px;
  outline: 0;
  width: 100%;
  border: none;
}
.Textarea {
  margin: 0;
  height: auto;
  min-height: 180px;
  line-height: 15px;
  outline: 0;
  width: 100%;
  border: none;
  resize: none;
}
.codeWrap {
  position: relative;
}
.codeWrap .CodeMirror {
  margin: 0;
}
.codeWrap:after {
  content: ' ';
  position: absolute;
  top: 0;
  width: 29px;
  background: #f7f7f7;
  border-right: 1px solid #ddd;
  left: 0;
  height: 100%;
  z-index: 0;
}
/*
    responsive settings
*/
@media screen and (max-width: 1130px) {
  .Topbar,
  .logo {
    height: calc(60px * 0.9);
  }
  .icon {
    width: calc(60px * 0.9);
    height: calc(60px * 0.9);
  }
  .Container {
    top: calc(60px * 0.9);
  }
}
@media screen and (max-width: 1000px) {
  .Topbar,
  .logo {
    height: calc(60px * 0.85);
  }
  .icon {
    width: calc(60px * 0.85);
    height: calc(60px * 0.85);
  }
  .Container {
    top: calc(60px * 0.85);
  }
}
@media screen and (max-width: 900px) {
  .Topbar,
  .logo {
    height: calc(60px * 0.8);
  }
  .icon {
    width: calc(60px * 0.8);
    height: calc(60px * 0.8);
  }
  .Container {
    top: calc(60px * 0.8);
  }
}
@media screen and (max-width: 800px) {
  .Topbar,
  .logo {
    height: calc(60px * 0.75);
  }
  .icon {
    width: calc(60px * 0.75);
    height: calc(60px * 0.75);
  }
  .Container {
    top: calc(60px * 0.75);
  }
}
@media screen and (max-width: 700px) {
  .Topbar,
  .logo {
    height: calc(60px * 0.7);
  }
  .icon {
    width: calc(60px * 0.7);
    height: calc(60px * 0.7);
  }
  .Container {
    top: calc(60px * 0.7);
  }
}
@media screen and (max-width: 600px) {
  .Topbar,
  .logo {
    height: calc(60px * 0.6);
  }
  .icon {
    width: calc(60px * 0.6);
    height: calc(60px * 0.6);
  }
  .Container {
    top: calc(60px * 0.6);
  }
}
